/* css-side-effects-plugin-ns:src/app/styles/index.css */
.grid {
  display: grid;
  grid-template-columns: 16fr 4fr 3fr 4fr;
  border: 1px solid var(--chakra-colors-black);
  border-top: none;
  padding: 0px 10px;
}
.grid > div {
  padding: 4px;
  align-self: center;
}
.grid.heading {
  border-top: 1px solid var(--chakra-colors-black);
}
input.quantity {
  text-align: right;
  width: 60px;
  padding: 4px;
  background-color: transparent;
  border: 1px solid;
  border-radius: 6px;
  font-size: 1rem !important;
}
.text-center {
  text-align: center;
}
.text-right {
  text-align: right;
}
.text-left {
  text-align: left;
}
.word-break-all {
  white-space: normal;
  word-break: break-all;
}
[type=search]::-webkit-search-decoration,
[type=search]::-webkit-search-cancel-button {
  appearance: none;
}
@media screen and (max-width: 30em) {
  .grid {
    grid-template-columns: 3fr 1fr 2fr;
    font-size: 0.8rem;
  }
  .grid > div {
    padding: 4px 8px;
  }
  .hidden-on-base {
    display: none;
  }
  input.quantity {
    font-size: 0.8rem !important;
    width: 100%;
  }
}
@media screen and (max-width: 48em) {
  .grid {
    grid-template-columns: 3fr 1fr 2fr;
  }
  .hidden-on-base {
    display: none;
  }
  input.quantity {
    font-size: 0.8rem !important;
    width: 100%;
  }
}
